
body {
  margin: 0;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont,
             'avenir next', avenir,
             helvetica, 'helvetica neue',
             Ubuntu,
             'segoe ui', arial,
             sans-serif;
* {
    font-weight: 300;
}
}
.users {
  background-color: #f0f0f0;
.user {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 10px;
    border-bottom: 1px solid #e2e2e2;
.avatar {
      margin-right: 10px;
}
}
}
.mugen-scroll {
  padding: 10px;
  font-size: 18px;
}
.count {
  position: fixed;
  bottom: 10px;
  right: 10px;
  font-size: 18px;
}
.wrap {
  max-width: 400px;
  height: 300px;
  margin: 0 auto;
  overflow-y: auto;
}



